import { Component, OnInit } from '@angular/core';
import { BatchService } from '../../services/batch.service';
import { UserService } from '../../services/user.service';

@Component({
  selector: 'app-intern-opgeleverde-batches',
  templateUrl: './intern-opgeleverde-batches.component.html',
  styleUrls: ['./intern-opgeleverde-batches.component.css']
})
export class InternOpgeleverdeBatchesComponent implements OnInit {

  batches = [];
  controleurs = [];

  constructor(public batchService: BatchService, public userService: UserService) { }

  ngOnInit() {
    this.batchService.getInternOpgeleverdeBatches().subscribe(res => {
      this.batches = res;
    });

    this.userService.getAllControleurs().subscribe(res => {
      this.controleurs = res;
    });
  }

}



