import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

/**
 * Services
 */
import { AuthenticationService } from './services/authentication.service';
import { BatchService } from './services/batch.service';
import { UserService } from './services/user.service';

/**
 * Angular Components
 */
import { AppComponent } from './app.component';
import { AuthenticationComponent } from './views/authentication/authentication.component';
import { DashboardComponent } from './views/dashboard/dashboard.component';
import { InternOpgeleverdeBatchesComponent } from './views/intern-opgeleverde-batches/intern-opgeleverde-batches.component';
import { TransformIdPipe } from './pipes/transform-id.pipe';

const appRoutes: Routes = [
  { path: '', component: AuthenticationComponent },
  { path: 'dashboard', component: DashboardComponent },
  { path: 'intern-opgeleverde-batches', component: InternOpgeleverdeBatchesComponent },
  { path: '**', component: DashboardComponent }
];

@NgModule({
  declarations: [
    AppComponent,
    AuthenticationComponent,
    DashboardComponent,
    InternOpgeleverdeBatchesComponent,
    TransformIdPipe
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(appRoutes, { enableTracing: true }), // <-- debugging purposes only
    FormsModule,
    HttpModule
  ],
  providers: [
    AuthenticationService,
    BatchService,
    UserService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



